

















































import { Component, Vue, Watch } from "vue-property-decorator";
import http from "@/services/HttpService";
import { namespace } from "vuex-class";
const Auth = namespace("Auth");

interface MessageItem {
  content: string;
  date: string;
  user_id: number;
  id: number;
  replies: any;
  user: User;
}

interface User {
  account: string;
  name: string;
  id: number;
}

@Component({
  components: {},
})
export default class MessageDialog extends Vue {
  public visible: boolean = false;
  public response: MessageItem = {
    content: "",
    date: "",
    user_id: 0,
    id: 0,
    replies: [],
    user: {
      account: "",
      id: 0,
      name: "",
    },
  };
  public loading: boolean = false;
  public reply: string = "";

  @Auth.State
  private user!: any;

  public open(id: number) {
    this.visible = true;
    this.response = {
      content: "",
      date: "",
      user_id: 0,
      id: 0,
      replies: [],
      user: {
        account: "",
        id: 0,
        name: "",
      },
    };
    this.reload(id);
  }

  public reload(id: number) {
    this.loading = true;
    const data = {
      id: id,
    };
    http
      .get("message/find", data)
      .then((res: any) => {
        this.response = res.data[0];
      })
      .finally(() => {
        console.log(this.response);
        this.loading = false;
      });
  }

  public submit() {
    const data = {
      reply: this.reply,
      message_id: this.response.id,
    };

    http.post("message/reply/add", data).then((res: any) => {
      this.reply = "";
      this.reload(this.response.id);
    });
  }

  public close(){
    this.visible = false;
    this.$emit('close');
  }
}
